import React,{Component} from 'react'
import '../contact/contact.css'
import './book-now.css'
import CabList from '../cab/cab-list'
import Http from '../../service/http'
import {connect} from 'react-redux'
import io from "socket.io-client";

const HOST = "/";
let socket = io.connect(HOST);

class BookNow extends Component{
    constructor(){
        super()
        this.state={
                start:"",
                end:"",
                searchcab:false,
                joinLike:{},
                isjoined:false,
                isliked:false,

            }
          //  this.handleInputChange = this.handleInputChange.bind(this);
        
    }


    componentDidMount(){
        socket.on('joinedLiked',(data)=>{
            console.log(data)
            this.props.updateCabs(data.event)

        })
        socket.on('newEvent',(data)=>{
            console.log(data)
            this.props.updateCabs(data.event)

        })
        Http.get('cab',(resp)=>{
            if(resp.status  && resp.status == 200)
            this.props.getCabs(resp.data)
        })
    }
    handleBooking(cab){
        console.log(cab)
        this.props.updateCabs(cab)
        socket.emit('join-event',{
            client:sessionStorage.getItem('email'),
            count : cab.joined+1,
            cabId : cab._id,
            joined:true
        })
       
    }
    handleLike(cab){
        console.log(cab)
        this.props.updateCabs(cab)
        socket.emit('join-event',{
            client:sessionStorage.getItem('email'),
            count : cab.likes+1,
            cabId : cab._id,
            liked:true
        })
       
    }
    render(){
         return <div className="parallax-section content">
        <div className="container">
            <div className="row">
                
                <div className="col-md-9 col-sm-9 wow fadeInUp search-results" data-wow-delay="0.6s">
    
                <CabList state = {this.state} handleLike = {this.handleLike.bind(this)} handleBook={this.handleBooking.bind(this)}/>
            </div>
               
            </div>
           
        </div>
    </div>
    }
}

function mapDispatchToProps(dispatch) {
    return{
        getCabs:(data)=>{
            dispatch({
                type:"Get_Cabs",payload:data
            })
        },
        updateCabs:(data)=>{
            dispatch({
                type:"Update_Cabs",payload:data
            })
        }
    }
    
}


export default connect(null,mapDispatchToProps)(BookNow)
