import React,{Component} from 'react'
import './contact.css'
import Http from '../../service/http'

class NewEvent extends Component{
    constructor(props){
        super(props)
        this.state={
            eventName:"",
                email:"",
                sponsorName:"",
                location:"",
                description:""
            }
            this.handleInputChange = this.handleInputChange.bind(this);
        
    }
    handleInputChange(event) {
        const target = event.target;
        const value =  target.value;
        const name = target.name;
    
        this.setState({
            [name]: value
        });
      }

      handleSubmit=()=>{
        let contactForm = {
           "eventName": this.state.eventName,
            "sponsorName": this.state.sponsorName,
            "location": this.state.location,
            "description": this.state.description
        }
        Http.post('cab',contactForm,
             (resp)=>{
               console.log(resp)
               if(resp.status == 201){
                this.props.history.push('/booknow');
               }
             })

    }
    render(){
        
        return <div className="parallax-section content">
        <div className="container">
            <div className="row contact">
                <div className="col-md-5 col-sm-5 wow fadeInUp" data-wow-delay="0.6s">
                <h2 className="heading">create event</h2>
                <div>
       
       <form className="form-group">
       <div className="">
       <p>event name</p>
       <input type="text" className="form-control"  name="eventName"  value={this.state.eventName}
       onChange={this.handleInputChange}  />
       </div>
       <div className="submit">
       <p>sponsor name</p>
       <input type="text" className="form-control"  name="sponsorName"  value={this.state.sponsorName}
       onChange={this.handleInputChange}  />
       </div>
     <div className="submit">
     <p>location</p>
     <input type="text" className="form-control" name="location" value={this.state.location}
     onChange={this.handleInputChange}  ></input>
     </div>
     <div className="submit">
     <p>description</p>
     <textarea type="text" className="form-control" name="description" value={this.state.description}
      onChange={this.handleInputChange}  ></textarea>
     </div>
     <div className="submit">
     <button type="button"  className="btn btn-success" onClick={this.handleSubmit} >submit</button>
     </div>
     
     </form>
       </div>
                </div>
               
           
            </div>
            
        </div>
    </div>
    }
}


export default NewEvent;