import React,{Component} from 'react'
import {connect} from 'react-redux'
import './cab-list.css'

class CabList extends Component {


    
   
    render(){
        console.log(this.props.cabs[0] && this.props.cabs[0].joiners && this.props.cabs[0].joiners.indexOf(sessionStorage.getItem('email')))
        let cabs = <p>loading events....</p>
        if(this.props.cabs && this.props.cabs.length>0)
         cabs = this.props.cabs.map((cab)=>{
            return <section id="item"  key={cab._id} className="wow fadeInUp" data-wow-delay="0.3s">
            <div className="card mb-3">
            <div className="row">
              <div className="col-md-4 cab-detail">
              <img src="https://pngimage.net/wp-content/uploads/2018/05/event-png-1.png" className="img-cvr" alt="..." width="10%" height="20%"/>
              <div className="submit">
                  {cab.joiners && cab.joiners.indexOf(sessionStorage.getItem('email'))>-1?<button type="button"  className="btn btn-success" disabled>joined</button>:<button type="button"  className="btn btn-success" onClick={()=>this.props.handleBook(cab)} >join</button>}
                  {cab.likers && cab.likers.indexOf(sessionStorage.getItem('email'))>-1?<button type="button"  className="btn btn-success" disabled >liked</button>:<button type="button"  className="btn btn-success" onClick={()=>this.props.handleLike(cab)} >like</button>}
              
              </div>
              <span className="card-footer">Joined {cab.joined } Likes {cab.likes }</span>
              </div>
              <div className="col-md-6">
                <div className="card-body">
                  <h5 className="card-title">Event:{cab.eventName}</h5>
                    <span className="amount">Location:{cab.location}</span>
                     <p className="card-text"> Sponsor:{cab.sponsorName}</p>
                  <p className="card-text"> {cab.description}</p>
                </div>
                
              </div>
            </div>
          </div>
           
           
            </section> 
            
            })
         
        return<section id="" className="parallax-section">
           
       {cabs}
    </section>

    }

   

}
function mapStateToProps(state) {
//console.log(state)
    return {
        cabs:state.cabReducer
    }
}

function mapDispatchToProps(dispatch) {
    return{
        bookCab:(cab_id)=>{
            dispatch({
                type:"Book_Cab",payload:cab_id
            })
        },
        getCabs:(data)=>{
            dispatch({
                type:"Get_Cabs",payload:data
            })
        }
    }
    
}


export default connect(mapStateToProps,mapDispatchToProps)(CabList)